// button
import vuePoster from './poster'
import vuePosterItem from './item'
import VueDragResize from './vue-drag-resize'

// 
const components = [
    VueDragResize,
    vuePoster,
    vuePosterItem,
]

//  install  Vue  use 
const install = function (Vue) {
    // 
    if (install.installed) return
    // 
    components.map(component => Vue.component(component.name, component))

    Vue.config.ignoredElements = ['vpNode']
}

// 
if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue)
}

export default {
    //  install Vue.use() 
    install,
    // 
    VueDragResize,
    vuePoster,
    vuePosterItem,
}
